import React, { useEffect, useRef, useState } from "react"
import { Container } from "react-bootstrap"

import { useHeaderData } from "hooks/contentful/useHeaderData"

import UtilityNav from "./UtilityNav/UtilityNav"
import MobileUtilityNav from "./MobileUtilityNav/MobileUtilityNav"

import HeaderNav from "./HeaderNav/HeaderNav"

import styles from "./Header.module.scss"

const EvergreenMessage = ({ message, reference }) => (
  <Container
    ref={reference}
    fluid
    as={"section"}
    className={styles.audienceContainer}
  >
    <Container>
      <p className={styles.audience}>{message}</p>
    </Container>
  </Container>
)

const AnnouncementMessage = ({ message, reference, projectCode }) => (
  <Container
    fluid
    as={"section"}
    className={styles.announcementContainer}
    ref={reference}
  >
    <Container>
      <p className={styles.announcement}>{message}</p>
      <p className={styles.projectCode}>{projectCode}</p>
    </Container>
  </Container>
)

const Header = () => {
  const { evergreenMessage } = useHeaderData()
  const announcementReference = useRef(null)
  const evergreenReference = useRef(null)
  const utilityReference = useRef(null)
  const [headerOffset, setHeaderOffset] = useState(0)

  useEffect(() => {
    setHeaderOffset(
      evergreenReference.current.offsetHeight +
      announcementReference.current.offsetHeight +
        utilityReference.current.offsetHeight
    )
  }, [evergreenReference, announcementReference, utilityReference])

  return (
    <>
      <AnnouncementMessage 
        message={'Visit the Aimmune Virtual Booth at ACAAI: November 13–15'} 
        projectCode={'AIMT-PM-USA-0561 10/20'}
        reference={announcementReference}/>
      <EvergreenMessage
        reference={evergreenReference}
        message={evergreenMessage}
      />
      <UtilityNav />
      <MobileUtilityNav reference={utilityReference} />
      <HeaderNav headerOffset={headerOffset} />
    </>
  )
}

export default Header
